.node {
  width: 3vh;
  height: 3vh;
  border-radius: 50%;
  position: absolute;
  background: black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: #fff;
  font-size: 16px;
  z-index: 2;
  transform: translateX(-50%) translateY(-50%);
}

.container {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 8vh;
  word-break: normal;
  z-index: 1;
  overflow: hidden;
}

.bone {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  animation: blink 0.1s step-start 0s infinite;
}

.boneLine {
  stroke-width: 0.5%;
  animation: blink 0.1s step-start 0s infinite;
}

.tag {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: #fff;
  font-size: 14px;
  z-index: 2;
  transform: translateX(-50%) translateY(-50%);
  padding: 1em;
  text-align: center;
}

.eyeDist {
  margin: 0 0 0 10px;
  background-color: black;
  color: white;
  padding: 10px;
}
